import Vue from 'vue'
import Router from 'vue-router'


import Home from '@/vieuws/Home'
import Signify from '@/vieuws/Signify'
import Broadcast from '@/vieuws/Broadcast'

Vue.use(Router)

export default new Router({
    routes:[
        {
            path:'/',
            name: '/Home',
            component: Home
        },
        {
            path:'/signify',
            name: '/Sifnify',
            component: Signify
        },
        {
            path:'/broadcast',
            name: '/Broadcast',
            component: Broadcast
        },
        
        

    ]
})