<template>

<div class="app" v-bind:style="{ backgroundColor: this.color}">

</div>
  
</template>
<script src="../../node_modules/osc/dist/osc-browser.min.js"></script>
<script src="https://ajax.googleapis.com/ajax/libs/jquery/3.3.1/jquery.min.js"></script>

            
<script>
import osc from "osc";
import $ from 'jquery'

 var port = new osc.WebSocketPort({
          url: "ws:178.62.209.37:8083" //localhost:8083 online server 178.62.209.37:8083
        }); 

 port.open();


export default {
  

  data() {
  return{
    value: 60,
    addressen:[],
    rood:'',
    address:'',
    groen:'',
    address2:'',
    blauw:'',
    color:''
    
    
     
    
  }},
   created: function(){
             this.OSCMessage();      
          //console.log(message);

     },
  computed: {

   
    total: function () {
        
      return this.value 
    }
  },
  methods:{
    greet: function(){
      
      port.send({
        address: "/hello",
          args: ["De slider werkt!"]
         });
    },
      sendSlider: function(){

        port.send({
        address: "/hello",
          args: [this.value]
         });

     },
      OSCMessage: function(){ 
               
        port.on("message", (oscMessage) => {
          
             //console.log(oscMessage);
             this.OSCMessages(oscMessage);
          
      });
     },
      OSCMessages: function(oscMessage){ 
        //console.log(oscMessage); 
         
        // var n = this.addressen.includes(oscMessage.address) // looks if address exist in the array if not N false otherwise it is true
         
        // //console.log(oscMessage.address);
        // if(n == false) {   // if n is false address dont exist en will add new adress in the array                    
        //     this.addressen.push(oscMessage.address);            
        // }
//console.log(oscMessage); 
        // first address with data
      if(oscMessage.address == "/rood" ){
          this.rood= oscMessage.args[0]
          //this.address= oscMessage.address
          //console.log(this.rood); 
        }

    // second address with data
      if(oscMessage.address == "/groen" ){
          this.groen= oscMessage.args[0]
         // this.address2= oscMessage.address
          //console.log(this.message); 
        }
        // second address with data
      if(oscMessage.address == "/blauw" ){
          this.blauw= oscMessage.args[0]
        
          //console.log(this.message); 
        }
        this.color = 'rgb('+this.rood+','+this.groen+','+this.blauw+')'
        // console.log( this.color); 
     }, 
   
  },
  
}


</script>

